import * as React from 'react';
import { SVGProps } from 'react';

const SvgFunctionActivate = (props: SVGProps<SVGSVGElement>) => (
    <svg
        width="1em"
        height="1em"
        viewBox="0 0 32 32"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        {...props}
    >
        <rect
            x={26}
            y={15}
            width={2}
            height={20}
            rx={1}
            transform="rotate(90 26 15)"
            fill="currentColor"
        />
    </svg>
);

export default SvgFunctionActivate;
